.i {
  display: flex;
  height: 560px;
  margin-bottom: 1rem;
}

.i-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.i-right {
  flex: 1;
  position: relative;
}

.i-left-wrapper {
  padding: 50px;
  height: 60%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.i-intro {
  font-size: 30px;
  font-weight: 300;
}

.i-name {
  font-size: 60px;
}

.i-title {
   height: 40px;
   overflow: hidden;
}

.i-title-wrapper {
  height: 40px;
  animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
  25% {
    transform: translateY(-50px);
  }
  50% {
    transform: translateY(-100px);
  }
  75% {
    transform: translateY(-150px);
  }
  100% {
    transform: translateY(-200px);
  }
}

.i-title-item {
  height: 50px;
  font-size: 30px;
  font-weight: bold;
  color: #36cca2;
  display: flex;
  align-items: center;
}

.i-img {
  transform: translateX(-0px) translateY(-0px);
  width: 108%;
}

.shape{
 margin: 5rem 0 0 8rem;
 width:  400px;
}

.resume {
  padding: 0.5rem;
  margin-top: 1.5rem;
  background: #36cca2;
  border-radius: 1rem;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
}
