.p {
  width: 46%;
  height: 310px;
  margin: 20px 10px;
  border: 1.5px solid rgb(243, 242, 242);
  border-radius: 10px 10px 0px 0px;
  overflow: hidden;
  position: relative;
}

.p-browser {
  position: relative;
  height: 20px;
  background-color: rgb(209, 209, 209);
  display: flex;
  align-items: center;
  position: sticky;
  z-index: 2;
}

.p-circle {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  margin: 3px;
  background-color: white;
}

.p-img {
  width: 100%;
  height: 240px;
  transition: all 2s ease;
}

.p:hover .p-img {
  transform: translateY(-52px);
}
.code{
  align-items: center;
  border: 1.3px solid #ccc;
  border-radius: .4rem;
  cursor: pointer;
  color: #333;
  display: flex;
  font-family: sans-serif;
  justify-content: center;
  text-decoration: none;
  position: absolute;
  background: #fff;
  padding: .35rem .4rem;
  right: 20px;
  top: 270px;
}
.code svg{
 margin-right: 4px;
 width: 20px;
 height: 20px;
}
.tstack{
  margin: 0 ;
  width: 70%;
  height: 40px;
}
.tstack img{
  width: 30px;
  margin: 4px;
}


